/*------------------------------------
    Header
------------------------------------*/

.app-header {
    color: $base-1;
    .header-logo {
        top: 30px;
        left: 30px;
        z-index: 99;
        position: fixed;
        a {
            font-size: 24px;
            font-weight: 700;
            line-height: 1;
            border-bottom: 6px solid $primary;
        }
    }
    .header-nav {
        top: 50%;
        left: 30px;
        padding: 0;
        z-index: 99;
        position: fixed;
        list-style: none;
        transform: translate(-50%, -50%);
        li {
            height: 40px;
            font-size: 14px;
            position: relative;
            a {
                display: block;
                position: relative;

                span {
                    top: 10px;
                    left: 18px;
                    position: absolute;
                    white-space: nowrap;
                }
                .anchor {
                    display: none;
                }
                &:before {
                    top: 15px;
                    left: -4px;
                    width: 8px;
                    height: 8px;
                    content: "";
                    position: absolute;
                    border-radius: 50%;
                    border: 1px solid $base-1;
                }
            }
            &:hover {
                .count {
                    display: none;
                }
                .anchor {
                    display: block;
                }
            }
            .active {
                color: $primary-1;
                border-color: $primary-1;
                background-color: $primary;

                .count {
                    display: none;
                }
                .anchor {
                    display: block;
                }
                &:before {
                    border-color: $primary-1;
                }
            }
        }
    }
    .button-demo {
        top: 25px;
        right: 30px;
        z-index: 99;
        position: absolute;
    }
    .button-menu {
        top: 10px;
        right: 30px;
        width: 50px;
        height: 50px;
        z-index: 99;
        position: fixed;
        padding-top: 12px;
        span {
            width: 36px;
            height: 2px;
            float: right;
            z-index: 3;
            display: block;
            position: relative;
            background-color: $base-1;
            &:before {
                width: 36px;
                height: 2px;
                float: right;
                z-index: 3;
                display: block;
                position: relative;
                background-color: $base-1;
                top: -12px;
                width: 30px;
                content: "";
            }
            &:after {
                width: 36px;
                height: 2px;
                float: right;
                z-index: 3;
                display: block;
                position: relative;
                background-color: $base-1;
                width: 30px;
                bottom: -10px;
                content: "";
            }
        }
    }
}

// Header 1
.app-header-1 {
    &:after {
        top: 0;
        left: 0;
        width: 100%;
        height: 84px;
        z-index: 6;
        opacity: 0.6;
        content: "";
        position: fixed;
        background: linear-gradient(180deg, $base-0 0%, transparent 100%);
    }
}

// Header 2
.app-header-2 {
    .header-logo {
        a {
            border-bottom: 6px solid $primary;
        }
    }
}
.home-2 {
    .app-header {
        .header-nav {
            li {
                .active {
                    font-weight: 700 !important;
                    
                    &:before {
                        border-color: $primary;
                        background-color: $primary !important;
                    }
                }
            }
        }
    }
}

// Header 4
.app-header-4 {
    .header-logo {
        a {
            border-bottom: 6px solid $primary;
        }
    }
}

// Header 5
.home-5 {
    .app-header {
        .header-nav {
            li {
                .active {
                    color: $black !important;
                    font-weight: 700 !important;
                    
                    &:before {
                        border-color: $black;
                        background-color: $black !important;
                    }
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .app-header {
        .header-nav {
            left: 15px;
        }
        .header-logo {
            left: 15px;
        }
        .button-menu {
            top: 25px;
            right: 15px;
        }
    }
    .site-sidebar {
        .site-sidebar-search {
            display: none;
        }
    }
}
@media (max-width: 992px) {
    .app-header {
        .header-logo {
            left: 18px;
        }
        .header-menu {
            top: 7px;
            right: 10px;
        }
        .header-follow {
            left: 10px;
        }
        .header-nav {
            display: none;
        }
        .header-follow {
            display: none;
        }
    }
}
